    font-size: 19px;
    font-weight: 500;
    font-style: normal;
    line-height: 25px;
    padding: 13px 20px;
    position: relative;
    text-transform: uppercase;
    z-index: 4;
}
.menu-area ul li a i {
  padding-left: 10px;
}
