.menu-area ul li a::before {
  display: none;
}
.menu-area ul li a::after {
  display: none;
}
.safe-area {
  display: inline-block;
  float: center;
  position: relative;
}

.menu-area ul li::before,
.safe-area a::before {
    display: none;
}
.safe-area a {
  font-size: 16px;
  font-weight: 700;
}

.menu-area ul li {
  margin-right: 8px;
}
.menu-area ul li:hover>a,
.menu-area ul li.active>a {
    color: #499e46;
}
.menu-area ul li .mega-menu span a:hover,
.menu-area ul li .sub-menu ul li a:hover {
    color: #499e46;
}

/* .safe-area a {
  position: relative;
  z-index: 4
} */
.menu-area ul li .mega-menu {
  background: #fff none repeat scroll 0 0;
  box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, 0.1);
  padding: 30px 20px;
  position: absolute;
  z-index: 6;
  width: 910px;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0 0;
  transition: .5s;
  visibility: hidden;
}
.menu-area ul li:hover .mega-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1);
}

.menu-area ul li .mega-menu span {
  float: center;
  width: 25%;
  padding-right: 20px;
}
.menu-area ul li .mega-menu span a.title {
  border-bottom: 1px solid #e5e5e5;
  color: #393939;
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  margin-bottom: 12px;
  padding-bottom: 15px;
  text-transform: uppercase;
}
.menu-area ul li .mega-menu span a {
  display: block;
  color: #777;
  font-size: 14px;
  line-height: 30px;
  text-transform: capitalize;
  transition: .3s;
  text-decoration: none;
}
.menu-area ul li .mega-menu span a:hover,
.menu-area ul li .sub-menu ul li a:hover {
    color: #24902f;
}

.menu-area ul li .mega-menu span a:before {
    display: none;
}

.menu-area ul li .mega-menu span a:after {
    display: none;
}

.menu-area ul li .mega-menu-2 {
    width: 690px;
}

.menu-area ul li .mega-menu-2 span {
    width: 33.33%;
}

.menu-area ul li .mega-menu-3 {
    width: 460px;
}

.menu-area ul li .mega-menu-3 span {
    width: 50%;
}

.menu-area ul li .sub-menu {
  background: #fff;
  box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, 0.1);
  left: 0;
  position: absolute;
  top: 100%;
  z-index: 45;
  padding: 10px 20px 15px;
  position: absolute;
  z-index: 6;
  transform: scaleY(0);
  transform-origin: 0 0 0;
  transition: .5s;
  width: 200px;
  opacity: 0;
  visibility: hidden;
}
.menu-area ul li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1);
}
.menu-area ul li .sub-menu ul li {
  display: block;
}

.menu-area ul li .sub-menu ul li:before {
  display: none;
}
.menu-area ul li .sub-menu ul li a {
  color: #777;
  display: block;
  font-size: 14px;
  font-weight: 400;
  padding: 5px 0 3px;
  text-decoration: none;
  transition: .3s;
  text-transform: capitalize;
}

.menu-area ul li .sub-menu ul li a:before {
  display: none;
}

.menu-area ul li .sub-menu ul li a:after {
  display: none;
}
.main-menu-area.sticky {
  left: 0;
  position: fixed;
  top: 0;
  transition: .5s;
  width: 100%;
  z-index: 99999999;
}
/*----------------------------------------*/
/*  3. Banner area
/*----------------------------------------*/

.info-container{
  max-width: 940px !important;
}

.single-banner {
  display: flex;
}

.single-banner .banner-img {
  margin-right: 12px;
  margin-top: 4px;
}

.single-banner .banner-img {
  flex: 0 0 32px;
  margin-top: 4px;
}

.single-banner .banner-text {}

.single-banner .banner-text h4 {
  color: #222;
  font-family: "Rufina", serif;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 2px;
}

.single-banner .banner-text p {
  color: #333;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 0;
}

.banner-img-2 a {
  border-radius: 3px;
  display: block;
  max-width: 100%;
  overflow: hidden;
  position: relative;
}

.banner-img-2 a img {
  width: 100%;
  transform: scale(1);
  transition: .5s;
}

.banner-img-2:hover a img {
  transform: scale(1.1);
}

.banner-shadow-hover {
  position: relative;
}

.banner-shadow-hover::before {
  bottom: 0;
  content: "";
  left: 0;
  margin: 0 auto;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  transition: all 300ms ease-in 0s;
}

.banner-shadow-hover:hover::before {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.7) inset;
  transition: all 300ms ease-in 0s;
}

.banner-area-5 .banner-text {
  left: 5%;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.banner-area-5 .banner-text h3 {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 7px;
  text-transform: capitalize;
}

.banner-area-5 .banner-text h2 {
  color: #fff;
  display: block;
  font-size: 28px;
  line-height: 1;
  margin-right: -218px;
  text-transform: uppercase;
  font-weight: 400;
}

/*----------------------------------------*/
/*  4. Slider area
/*----------------------------------------*/

.single-slider {
  position: relative;
}

.slider-img img a img {
  width: 100%;
}

.slider-content h1 {
  color: #333;
  font-size: 80px;
  font-weight: 700;
  line-height: 1;
  text-transform: capitalize;
  font-family: 'Rufina', serif;
  font-weight: 400;

}

.slider-content h2 {
  color: #333333;
  font-size: 100px;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 25px;
  text-transform: capitalize;
  font-family: 'Rufina', serif;
  font-weight: 400;
  
}

.slider-content h3 {
  color: #666;
  font-size: 30px;
  font-weight: 400;
  line-height: 1;
  text-transform: none;
  font-family: 'Rufina', serif;
  font-weight: 400;
}

.slider-content a {
  background: #499e46 none repeat scroll 0 0;
  border: 2px solid #499e46;
  border-radius: 4px;
  color: #fff;
  display: inline-block;
  font-size: 24px;
  font-weight: 400;
  line-height: 30px;
  margin-top: 43px;
  padding: 10px 38px;
  text-transform: none;
  font-family: 'Rufina', serif;
  font-weight: 400;
  text-decoration: none;
  box-shadow: 2px 2px 2px 2px #a8c0ba;
}

.slider-content a:hover {
  background: #333 none repeat scroll 0 0;
  border-color: #333;
  box-shadow: 2px 2px 2px 2px #a8c0ba;
  transition: all 300ms ease-in 0s;
}

.slider-content-2 h1 {
  font-size: 40px;
  text-transform: none;
}

.slider-content-2 h2 {
  color: #fff;
  font-size: 80px;
  text-transform: none;
}

.slider-active.owl-carousel .owl-nav .owl-prev {
  background-color: transparent;
  color: #fff;
  display: block;
  font-size: 60px;
  opacity: 0;
  visibility: hidden;
  padding: 0;
  left: 20px;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s ease-in-out 0s;
  z-index: 9;
  line-height: 1;
}

.slider-active.owl-carousel .owl-nav .owl-next {
  background-color: transparent;
  color: #fff;
  display: block;
  font-size: 60px;
  opacity: 0;
  visibility: hidden;
  padding: 0;
  left: auto;
  right: 20px;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s ease-in-out 0s;
  z-index: 9;
  line-height: 1;
}

.slider-active.owl-carousel .owl-nav .owl-prev:hover,
.slider-active.owl-carousel .owl-nav .owl-next:hover {
  color: #259030;
}

.slider-active.owl-carousel .owl-dots .owl-dot span {
  background: #fff none repeat scroll 0 0;
  border-radius: 50px;
  display: block;
  height: 10px;
  margin: 5px;
  opacity: 1;
  transition: all 0.3s ease 0s;
  width: 10px;
}

.slider-active.owl-carousel .owl-dots .owl-dot.active span {
  background: #259030 none repeat scroll 0 0;
  width: 32px;
}

.slider-active.owl-carousel .owl-dots .owl-dot {
  display: inline-block;
}

.slider-active.owl-carousel .owl-dots {
  bottom: 10px;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
  transition: .5s;
}

.slider-area:hover .slider-active.owl-carousel .owl-dots {
  opacity: 1;
  visibility: visible;
}

.slider-area:hover .slider-active.owl-carousel .owl-nav .owl-prev,
.slider-area:hover .slider-active.owl-carousel .owl-nav .owl-next {
  opacity: 1;
  visibility: visible;
}

.owl-item .slider-animated-1 * {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-item.active .slider-animated-1 h1 {
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
  
}

.owl-item.active .slider-animated-1 h2 {
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
  
}

.owl-item.active .slider-animated-1 h3 {
  -webkit-animation-delay: 1.1s;
  animation-delay: 1.1s;
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

.owl-item.active .slider-animated-1 a {
  -webkit-animation-delay: 1.4s;
  animation-delay: 1.4s;
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

.home-2 .owl-item.active .slider-animated-1 a,
.home-5 .owl-item.active .slider-animated-1 a {
  -webkit-animation-delay: 1.7s;
  animation-delay: 1.7s;
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

.home-2 .owl-item.active .slider-animated-1 p,
.home-5 .owl-item.active .slider-animated-1 p {
  -webkit-animation-delay: 1.4s;
  animation-delay: 1.4s;
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

/*----------------------------------------*/
/*  5. Product area
/*----------------------------------------*/
.section-title h2 {
    display: inline-block;
    font-size: 32px;
    font-weight: 700;
    line-height: 30px;
    position: relative;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.section-title p {
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 1px;
    line-height: 24px;
    margin-bottom: 0;
    font-family: 'Rufina', serif;
    color: #333;
}

.tab-menu ul li {
    display: inline-block;
    margin-right: 35px;
}

.tab-menu ul li a {
    color: #333;
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    padding-bottom: 5px;
    position: relative;
    text-transform: uppercase;
    text-decoration: none;
}

.tab-menu ul li a:before {
    background: #259030 none repeat scroll 0 0;
    bottom: -10px;
    content: "";
    height: 2px;
    left: 0;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    right: 0;
    transition: all 0.5s ease-in-out 0s;
    width: 100%;
}

.tab-menu ul li a:hover:before,
.tab-menu ul li a.active:before {
    opacity: 1;
    bottom: 0;
    visibility: visible;
}

.product-wrapper {
    position: relative;
    overflow: hidden;
}

.product-img,
.most-product-img {
    position: relative;
    overflow: hidden;
}

.product-img::after,
.most-product-img::after,
.banner-img-3::after {
    background: rgba(255, 255, 255, 0.61) none repeat scroll 0 0;
    content: "";
    display: block;
    height: 0;
    position: absolute;
    top: 0;
    transition: all 0.5s ease-in-out 0s;
    width: 100%;
    pointer-events: none;
}

.product-wrapper:hover .product-img::after,
.single-most-product:hover .most-product-img::after,
.single-deal:hover .banner-img-3::after {
    height: 100%;
    opacity: 1;
}

.product-img a img {
    width: 100%;
}

